import * as yup from 'yup';
import { ONLY_NUMBERS_REG_EXP } from './constants/regex';

export const createWalletSchema = yup.object().shape({
    walletName: yup.string().required('  '),
    walletStartSum: yup
        .string()
        .matches(ONLY_NUMBERS_REG_EXP, ' ')
        .required('   '),
});
